'use client';

import { useEffect, useState } from 'react';
import { useRouter, usePathname } from 'next/navigation';
import Link from 'next/link';
import { Menu, X, LayoutDashboard, Store } from 'lucide-react';

export default function AdminDashboard() {
  const router = useRouter();
  const pathname = usePathname();
  const [open, setOpen] = useState(true);

  useEffect(() => {
    const type = localStorage.getItem('user_type');
    if (type !== 'admin') {
      router.push('/admin/login');
    }
  }, [router]);

  const navItems = [
    { label: 'Insights', href: '/admin/dashboard', icon: <LayoutDashboard size={18} /> },
    { label: 'Manage Store', href: '/admin/dashboard/manage-store', icon: <Store size={18} /> },
  ];

  const handleLogout = () => {
    localStorage.clear();
    window.location.href = '/admin/login';
  };

  return (
    <div className="flex">
      <div
        className={`fixed top-0 left-0 h-full z-30 bg-gray-900 text-white transition-all duration-300
          ${open ? 'w-64' : 'w-16'}
        `}
      >
        <div className="flex items-center justify-between px-4 py-4 border-b border-white/10">
          <span className="font-bold text-lg whitespace-nowrap">
            {open ? 'AutoCare' : 'AC24'}
          </span>
          <button onClick={() => setOpen(!open)} className="ml-auto text-white">
            {open ? <X size={20} /> : <Menu size={20} />}
          </button>
        </div>

        <nav className="mt-6 flex flex-col gap-1 px-2">
          {navItems.map(({ label, href, icon }) => (
            <Link
              key={href}
              href={href}
              className={`flex items-center gap-3 rounded-md px-3 py-2 text-sm font-medium transition hover:bg-white hover:text-black
                ${pathname === href ? 'bg-white text-black' : 'text-white'}`}
            >
              {icon}
              {open && label}
            </Link>
          ))}

          <button
            onClick={handleLogout}
            className="mt-4 flex items-center gap-3 text-sm font-medium px-3 py-2 hover:bg-white hover:text-black rounded-md text-white"
          >
            ‚èª {open && 'Logout'}
          </button>
        </nav>
      </div>

      <main className={`ml-${open ? '64' : '16'} transition-all duration-300 w-full p-6`}>
        <h1 className="text-2xl font-bold">Welcome, Admin üöÄ</h1>
      </main>
    </div>
  );
}